.header {
  display: flex;
  justify-content: flex-end;
}
.header a {
  margin-right: 10px;
  font-weight: 200;
  text-decoration: none;
  font-size: 13px;
  color: #131516;
}
.header_nav {
  display: flex;
  flex: 3;
  align-content: center;
}
.header_nav .header_nav_logo {
  width: 100px;
  height: 25px;
  margin-left: 10px;
  align-content: center;
  flex: 1;
}
.header_nav .header_nav_logo img {
  margin: auto;
  width: 100px;
  height: 100%;
}
.header_nav_links {
  display: flex;
  justify-content: space-between;
  flex: 4;
}
.header_nav_links a {
  text-decoration: none;
  font-size: 15px;
  color: #131516;
  font-weight: 500;
}
.header_search_container {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}
.header_search_container input {
  margin: auto 0;
  border-radius: 5px;
  border-color: #dddfd4;
  height: 20px;
  border-style: solid;
}
.nav {
  display: flex;
}
